*{
  padding:0;
  margin:0;
}
ul,li,a{
  text-decoration: none;
  list-style: none;
  color:inherit;
}
img{
  width:100%;
}
/* header */

header{
  width:100%;
  position:fixed;
  top:0;
  left:0;
  z-index: 1000;
  padding: 0.5rem;
}
h1{
  font-size:1.5rem;
  float:left;
  cursor: pointer;
}
.nav>ul{
  display:flex;
  padding-left:2rem;
}
.nav>ul>li{
  padding:.3rem;
  text-align: center;
  border-radius: 10px;
}
.active{
  background-color:rgb(120, 120, 255);
  padding:.3rem;
  border-radius: 10px;
}
.nav>ul>li:not(:last-child){
  margin-right:1rem;
  padding:.3rem;
  border-radius: 10px;
}