/* about */
#about{
  width:22rem;
  height:22rem;
    display:table;
    margin:0 auto;
    box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
    margin-top:7rem;
    background-color: rgb(19, 19, 97);
    border-radius: 10px;
    box-sizing: border-box;
    transition: 0.2s ease-in-out;
  }
  #about:hover{
    transform:translateY(-10px);
    cursor:default;
    box-shadow: 5px 11px 30px rgba(0,0,0,0.8);
  }
  h2{
    font-size: 1px;
    line-height: 1px;
    visibility: hidden;
    display: none;
  }
  /* pc_size css */
  dt.title{
    width:100%;
    padding:2rem 2rem 1rem;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 10px 10px 0 0;
  }
  dt.title>p{
    padding-bottom:1rem;
  }
  .boxPc>dd{
    text-align: center;
    padding:1rem;
    color:#fff;
  }
  .boxPc>dd>p:last-child{
    padding-top:1.5rem;
  }
